.list {
    display: flex;
    flex-direction: column;
    padding: 0;
}

.listHeader {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
 }

.listElement {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid black;
    padding: 2px;
}

.listElement:not(:last-child){
    border-bottom: 0;
}

.listElementHeaderCompromised {
    text-decoration: line-through;
}

.listElementHeader {
    margin-right: 'auto'
}




